<template>
	<view class="abilityPortrait">
		<u-subsection :list="titlelist" mode="button" :current="current" @change="change" activeColor="#53A5D9">
		</u-subsection>
		<view v-if="current == '0'" class="content">

			<echarts class="uni-ec-canvas" id="line-chart" canvas-id="multi-charts-line" :ec="ringOption"></echarts>
			<button @click="updateClick">切换数据</button>
		</view>
	</view>
</template>

<script>
	// import echarts from 'c/echarts/echarts.vue'
	import echarts from "../uni-ec-canvas/uni-ec-canvas.vue"
	export default {
		name: 'abilityPortrait',
		components: {
			echarts
			// uniEcCanvas
		},
		props: {},
		data() {
			let dataMax = [{
					name: '易用性',
					max: '100'
				},
				{
					name: '功能',
					max: '100'
				},
				{
					name: '拍照',
					max: '100'
				},
				{
					name: '跑分',
					max: '102'
				},
				{
					name: '续航',
					max: '100'
				},
			]
			return {
				titlelist: ['评测报告', '个人知识结构'],
				current: 0,
				ringOption: {
					option: {
						//配置维度的最大值
						radar: {
							name: {
								show: true,
								color: 'red',
							},
							//   雷达图的指示器，用来指定雷达图中的多个变量（维度）
							indicator: dataMax,
							shape: 'circle', //对雷达图形设置成一个圆形,可选 circle:圆形,polygon:多角形(默认)
						},
						series: [{
							type: 'radar',
							label: {
								show: true, //显示数值
							},
							areaStyle: {}, //每个雷达图形成一个阴影的面积
							data: [{
									name: '华为手机',
									value: [80, 90, 80, 82, 90],
								},
								{
									name: '苹果手机',
									value: [70, 80, 88, 62, 98],
								},
							],
						}, ],
					},
				},
			}
		},
		methods: {
			change(index) {
				this.current = index
			},
		},
		watch: {},

		// 页面周期函数--监听页面加载
		onLoad() {},
		// 页面周期函数--监听页面初次渲染完成
		onReady() {},
		// 页面周期函数--监听页面显示(not-nvue)
		onShow() {},
		// 页面周期函数--监听页面隐藏
		onHide() {},
		// 页面周期函数--监听页面卸载
		onUnload() {},
		// 页面处理函数--监听用户下拉动作
		// onPullDownRefresh() { uni.stopPullDownRefresh(); },
		// 页面处理函数--监听用户上拉触底
		// onReachBottom() {},
		// 页面处理函数--监听页面滚动(not-nvue)
		// onPageScroll(event) {},
		// 页面处理函数--用户点击右上角分享
		// onShareAppMessage(options) {},
	}
</script>
<style scoped>
	.content {
		width: 400px;
		height: 600px;
	}

	.uni-ec-canvas {
		width: 100%;
		height: 100%;
		display: block;
	}
</style>
